@font-face {
    font-family: Sweetspot;
    src: url(Sweetspot.ttf);
}
@font-face {
    font-family: Shortstack;
    src: url(Shortstack.ttf);
}
* {
    margin: 0;
    padding: 0;
}
html
body {
    width: 100%;
    height: 100%;
    overflow: hidden;
    box-sizing: border-box;
}
body {
    background: #fc00ff;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #00dbde, #fc00ff);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #00dbde, #fc00ff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    font-family: Shortstack, sans-serif;
    cursor: url('./CURSOR.png'), auto;
}
a,
a:hover {
    cursor: url('./CURSOR.png'), auto;
}
h1 {
    font-family: Sodapop, sans-serif;
}
h1 span {
    position: relative;
    display: inline-block;
}
img {
    width: 100%;
    height: 100%;
}

#wrapper {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
#pop-up {
    width: 45%;
    height: 50%;
    background: url('./pop-up.png');
    background-size: 100% 100%;
    border-radius: 2rem;
    /* https://codersblock.com/blog/creating-glow-effects-with-css/ */
    box-shadow:
        0 0 50px #fff,      /* inner white */
        20px 0 80px #f0f,   /* inner left magenta short */
        -20px 0 80px #0ff,  /* inner right cyan short */
        20px 0 200px #f0f,  /* inner left magenta broad */
        -20px 0 200px #0ff, /* inner right cyan broad */
        0 0 50px #fff,            /* outer white */
        -10px 0 80px #f0f,        /* outer left magenta */
        10px 0 80px #0ff;         /* outer right cyan */
    overflow: hidden;
    text-align: center;
}
#imgContain {
    width: 112px;
    height: 112px;
    margin: 0 auto 3rem;
}
#pop-up-content {
    position: relative;
    margin: 4rem 0 0 1.5rem;
    color: #945bf1;
}
::selection {
    color: #0ff;
    background: #945bf1;
}